import { Pair } from 'bitecs';
import {
  setTraits,
  registerPostLoadHook,
  Sequence,
  SequenceState,
  Tween,
  TweenState,
  ChildOf,
  type State,
  type System,
  type Plugin,
  type ParseContext,
} from 'shallot';

export type SequenceMap = Record<string, string>;

export const StepController = {
  step: [] as number[],
  target: [] as number[],
  max: [] as number[],
};

setTraits(StepController, {
  defaults: () => ({ step: 0, target: 0, max: 0 }),
});

type EntityLookup = (name: string) => number | null;
let entityLookup: EntityLookup | null = null;

function captureEntityLookup(_state: State, context: ParseContext): void {
  entityLookup = (name: string) => context.getEntityByName(name);
}

registerPostLoadHook(captureEntityLookup);

export function getEntityByName(name: string): number | null {
  return entityLookup?.(name) ?? null;
}

function createStepControllerSystem(sequenceMap: SequenceMap): System {
  let isTransitioning = false;

  return {
    group: 'simulation',
    update(state: State) {
      if (isTransitioning) return;

      for (const eid of state.query([StepController])) {
        const current = StepController.step[eid];
        const target = StepController.target[eid];

        if (current !== target) {
          isTransitioning = true;

          const seqName = sequenceMap[`${current}-${target}`];
          const targetSeqEid = seqName ? getEntityByName(seqName) : null;

          for (const name of Object.values(sequenceMap)) {
            const seqEid = getEntityByName(name);
            if (seqEid !== null && state.hasComponent(seqEid, Sequence)) {
              if (Sequence.state[seqEid] === SequenceState.PLAYING) {
                Sequence.state[seqEid] = SequenceState.COMPLETE;
              }
            }
          }

          state.step(0);

          for (const name of Object.values(sequenceMap)) {
            const seqEid = getEntityByName(name);
            if (seqEid !== null && state.hasComponent(seqEid, Sequence)) {
              Sequence.state[seqEid] = SequenceState.IDLE;
              Sequence.elapsed[seqEid] = 0;
              for (const childEid of state.query([Pair(ChildOf.relation, seqEid), Tween])) {
                Tween.state[childEid] = TweenState.IDLE;
                Tween.elapsed[childEid] = 0;
              }
            }
          }

          if (targetSeqEid !== null && state.hasComponent(targetSeqEid, Sequence)) {
            Sequence.state[targetSeqEid] = SequenceState.PLAYING;
          }

          StepController.step[eid] = target;
          isTransitioning = false;
        }
      }
    },
  };
}

export function createStepPlugin(sequenceMap: SequenceMap): Plugin {
  return {
    components: { StepController },
    systems: [createStepControllerSystem(sequenceMap)],
  };
}
